* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
ul {
  list-style: none;
}
.country-list-item {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  font-size: 14px;
}
.head-container{
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding-left: 40px;
}
.country-name {
  font-size: 36px;
}
.list-item {
  font-size: 20px;
  font-weight: bold;
}
.country-flag-margin{
  margin-right: 10px;
}
